
.back-btn {
    height: 32px !important;
    width: 88px;
    border-radius: 16px;
    border: 1px solid #B7B7BC;
    cursor: pointer;
    color: #40404D;
    background: #F6F6F9;
    margin-top: 8px;
}

.proof-title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 40px;
    text-align: center;
    color: #101020;
    line-height: 48px;
}

.proof-actor-level-title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    color: #40404D;
    margin-inline: 6px;
}

.proof-actor-level {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    color: #40404D;
}

@media screen and (max-width: 576px) {
    .proof-card-main {
        display:inline-block;
        padding-top:24px;
        width:100%;
        background-color: #FFFFFF;
    }

    .proof-top {
        height: 48px;
        display: flex;
    }
    .proof-main {
        width: 100%;
        font-family: 'Montserrat' !important;
        font-style: normal !important;
        background-color: #F5F5F5;
    }
    .proof-context {
        padding: 20px 0px;
    }
    .proof-context-left {
        display: inline-block;
        margin-bottom: 6px;
    }

    .proof-context-right {
        padding-top: 12px;
    }

    .proof-context-table {
        background-color: #FFFFFF;
        border-radius: 8px;
        padding: 24px;
        margin-bottom: 16px;
    }

    .proof-user {
        width: 100%;
        background-color: #FFFFFF;
        padding: 0px 16px 16px 16px;
        border-radius: 8px;
    }

    .proof-user-avatar {
        height:128px;
        border-bottom: 1px dashed #B7B7BC;
    }

    .proof-user-icon {
        line-height: 128px;
        height: 80px;
        width: 80px;
        float: left;
    }

    .proof-user-title {
        float: left;
        padding-inline: 20px;
        padding-top: 8px;
    }

    .proof-user-name {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        color: #101020;
        padding-top: 26px;
        padding-bottom: 6px;
    }

    .proof-user-label {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        padding-top: 6px;
        padding-bottom: 6px;
        color: #101020;
    }

    .proof-user-link {
        line-height: 128px;
        float: right;
        display: none;
    }

    .proof-user-icon img {
        height: 80px;
        width: 80px;
        border: 2px solid #F6F6F9;
        border-radius: 60px;
        vertical-align: middle;
    }

    .proof-top-btn {
        float:left;
    }
    
    .proof-top-title {
        flex: 1;
        float:left;
        text-align: center;
        padding-right: 88px;
    }
}

@media screen and (min-width: 576px) {
    .proof-card-main {
        display:inline-block;
        width:100%;
        background-color: #FFFFFF;
    }
    .proof-top {
        height: 48px;
        display: flex;
    }
    .proof-main {
        width: 100%;
        padding-inline: 24px;
        font-family: 'Montserrat' !important;
        font-style: normal !important;
        background-color: #F5F5F5;
    }
    .proof-context {
        padding: 20px 0px;
        display: flex;
    }
    .proof-context-left {
        flex: 1;
        padding: 6px 12px 6px 0px;
    }
    .proof-context-right {
        flex: 1;
        padding: 6px 12px;
        padding: 6px 0px 6px 12px;
    }

    .proof-context-table {
        background-color: #FFFFFF;
        border-radius: 8px;
        padding: 4px 24px;
        margin-bottom: 24px;
    }

    .proof-user {
        width: 100%;
        min-height: 768px;
        background-color: #FFFFFF;
        padding: 0px 24px 24px;
        border-radius: 8px;
        
    }

    .proof-user-avatar {
        height:128px;
        padding:24px, 0px, 24px, 0px;
        border-bottom: 1px dashed #B7B7BC;
    }

    .proof-user-icon {
        line-height: 128px;
        height: 80px;
        width: 80px;
        float: left;
    }

    .proof-user-title {
        float: left;
        padding-inline: 20px;
        padding-top: 8px;
    }

    .proof-user-name {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        color: #101020;
        padding-top: 26px;
        padding-bottom: 6px;
    }

    .proof-user-label {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        padding-top: 6px;
        padding-bottom: 6px;
        color: #101020;
    }

    .proof-user-link {
        line-height: 128px;
        float: right;
    }

    .proof-user-icon img {
        height: 80px;
        width: 80px;
        border: 2px solid #F6F6F9;
        border-radius: 60px;
        vertical-align: middle;
    }

    .proof-top-btn {
        float:left;
    }
    
    .proof-top-title {
        flex: 1;
        float:left;
        text-align: center;
        padding-right: 88px;
    }

    .proof-card-main {
        display:inline-block;
        padding-top:24px;
        width:100%
    }
}

.proof-user-link span {
    cursor: pointer;
}