
@media screen and (max-width: 576px) {
    .detail-header {
        background-color: #FFFFFF;
        margin-top: 16px;
    }
    .detail-header-info {
        width: 100%;
        display: block;
    }
    .detail-header-info-title {
        color: #40404D;
        font-size: 16px;
        font-weight: 400;
        padding-block: 12px;
        display: block;
    }
    
    .detail-header-info-value {
        display: block;
        font-weight: 700;
        font-size: 16px;
        color: #101020;
        padding-block: 8px;
        margin-left: 28px;
        word-wrap: break-word;
        word-break: normal;
        max-width: 516px;
    }
}

@media screen and (min-width: 576px) {
    .detail-header {
        background-color: #FFFFFF;
        margin-top: 16px;
    }
    .detail-header-info {
        width: 100%;
        display: flex;
    }
    .detail-header-info-title {
        color: #40404D;
        font-size: 16px;
        font-weight: 400;
        vertical-align: top;
        min-width: 35%;
        padding-block: 12px;
    }
    
    .detail-header-info-value {
        flex: 1;
        font-weight: 700;
        font-size: 16px;
        text-align: right;
        color: #101020;
        text-align: right;
        padding-block: 8px;
    }
}

.detail-title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    color: #40404D;
}

.proof-url-link {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    text-decoration-line: underline;
    color: #00913A;
}